import axios from 'axios';
import qs from 'qs'; //序列化


// if(process.env.NODE_ENV == 'production'){
//     process.env.VUE_APP_API=
// }else{
//     process.env.VUE_APP_API=
// }
console.log(process.env);
const instance  = axios.create({
    baseURL: process.env.VUE_APP_API, // api的base_url
    timeout: 10000, // request timeout

})
// instance.defaults.headers['Content-Type'] = 'application/json'
// instance.defaults.headers['appname'] = 'MM7.3.2.001.01_CTAndroid_JT'
// instance.defaults.headers['ua'] = 'android-24-1080x2040-RNE-AL00'
instance.interceptors.request.use(config => {
    // Do something before request is sent
   // config.headers['biu-token'] = getToken() // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
    config.headers['Content-Type'] = 'application/json'
    config.headers['appname'] = 'MM7.3.1.001.01_CTAndroid_MAAP'
    config.headers['ua'] = 'android-24-1080x2040-RNE-AL00'

    return config
}, error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
})

instance.interceptors.response.use(
    res => {

        return res;

    },

    /**
     * 请求发生错误，一般都是服务器抛异常了
     */
    err => {
        console.error('request err: %o', err)// for debug

        return Promise.reject(error)
    }

)
export default instance

