<template>
    <div class="app-details">
        <div class="title">
            <div class="img-left">
                <img :src="dataList.iconUrl" alt="">
            </div>
            <div class="name-right">
                <div class="name">{{dataList.appName}}</div>
                <div class="middle">
                    <span>{{dataList.category}}</span> | <span>{{dataList.appSize}}M</span> | <span>{{dataList.interested}}</span>
                </div>
                <div class="grade">
                    <van-rate v-model="value"   :size="11.81" color="#fb9a27" readonly />  <span class="score">{{dataList.score}} 分</span>
                </div>
            </div>
        </div>
        <div  class="pic"  :images="dataList.previews" >
          <div v-for="(item) in dataList.previews" :key='item'  >
              <img :src=item  :preview="1" >
          </div>

        </div >

        <div class="desc">
            <div class="text">应用描述 :</div>
            <div class="descDetails" @click='fun_showAll'  :class="{ellpes:!showAll}">
               {{dataList.description}}
            </div>
        </div>
        <div class="about">
            <div class="update">
                <span> 更&nbsp; &nbsp;新</span>
                <span> {{dataList.updateTime |formatDate}}</span>
            </div>
            <div class="version">
                <span>版&nbsp; &nbsp;本</span>
                <span>{{dataList.version}}</span>
            </div>
            <div class="provider">
                <span>提供者</span>
                <span>{{dataList.provider}}</span>
            </div>
        </div>
        <div class="tag">
            <van-tag round plain v-for="item in dataList.plabels" :key="item" >{{item}}</van-tag>
            <!--<van-tag round plain>有广告</van-tag>-->
            <!--<van-tag round plain>白名单</van-tag>-->
            <!--<van-tag round plain>亲测可用</van-tag>-->
        </div>
        <div class="up">
            <span @click="fun_showAll">{{arrowMap[arrow]}} <van-icon :name="'arrow-'+arrow" /></span>
        </div>
        <div class='row_download'>
            <a :href='dataList.orderUrl' class="download">立即下载({{dataList.appSize}}MB)</a>
        </div>


    </div>
    
</template>

<script>
    import { Rate , Tag  ,Icon , } from 'vant';
    import appDetails from '@/api/appDetails'

    export default {
        components: {
            [Rate.name]: Rate,
            [Tag.name]: Tag,
            [Icon .name]: Icon ,
        },
        data() {
            return {
                value: 4,
                showAll:false,
                arrow:'down',
                arrowMap:{
                    down:'展开',
                    up:'收起'
                },
                dataList:{}
            }
        },
        created() {
            appDetails.getAppDetails({
                goodsid:this.$route.query.contentid
            }).then(res=>{
                console.log(res);
                if(res.status==200){
                    this.dataList = res.data
                    this.dataList.appSize=(res.data.appSize/1024).toFixed(2);
                    this.value=res.data.grade
                    console.log(this.dataList);
                    this.$previewRefresh()
                }
            })
        },
        filters: {
            formatDate: function (value) {
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                return y + '.' + MM + '.' + d
            }
        },
        methods: {
            fun_showAll(){
                this.showAll=!this.showAll;
                this.arrow = this.showAll? 'up' : 'down'
            },

        }
    };
</script>

<style scoped lang="less">
    .app-details{
        .title{
            height: 100px;
            border-bottom: 1px solid #e0e0e0;
            display: flex;
            .img-left{
                box-sizing: border-box;
                width: 88.19px;
                height: 100%;
                padding: 13.89px 13.89px 27px 13.89px;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            .name-right{
                flex: 1;
                height: 100%;
                box-sizing: border-box;
                padding-bottom: 27.78px;
                padding-top: 13.89px;
                .name{
                    box-sizing: border-box;
                    padding-top: 2.01px;
                    height: 18.1px;
                    font-size: 16.67px;
                    color: #212121;
                    margin-bottom: 8.42px;
                }
                .middle{
                    height:  13.19px;
                    font-size: 11.81px;
                    color: #9a9a9a;
                    margin-bottom: 4.22px;
                }
                .grade{
                    height: 11.89px;
                }
                .score{
                    margin-left: 10.41px;
                    font-size: 11.80px;
                    color: #fb9a27;
                }
            }
        }
        .pic::-webkit-scrollbar{
            display: none
        }
        .pic{
            overflow-x: scroll;
            overflow-y:hidden;
            display: -webkit-box;
            -webkit-overflow-scrolling: touch;
            height: 194.5px;
            margin: 10.41px 0 13.89px 13.89px;
            div{
                width: 109.72px;
                height: 100%;
                margin-right: 20.14px;
                img{
                    width:100%;
                    height: 100%;
                }
            }
        }
        .desc{
            margin: 13.89px;
            .text{
                font-size: 13.89px;
                font-weight: 500;
                margin-bottom: 4.13px;
            }
            .descDetails{
                font-size: 13.89px;
                color: #212121;
                line-height: 25.6px;
            }
        }
        .about{
            margin: 13.89px;
            font-size: 11.12px;
            color: #777777;
            div{
                margin-bottom: 10px;
                span:nth-child(1){
                    margin-right: 20px;
                }
            }
        }
        .tag{
            margin-left: 13.89px;
            .van-tag--round{
                height: 13.89px;
                font-size: 10.42px;
                margin-right: 10.42px;
            }
        }
        .up{
            text-align: center;
            font-size: 12.5px;
            color: #969696;
            margin: 13.89px 0 83.3px 0;
        }
        .row_download{
            position: fixed;
            bottom: 20.83px;
            width: 100%;
            text-align: center;
            z-index: 1;
            .download{
                display: inline-block;
                text-align: center;
                width: 277.78px;
                height: 100%;
                border-radius: 30px;
                line-height:48.62px;
                background-color: #3386e2;
                font-size: 16.67px;
                color: #ffffff;
            }
        }
    }
    .ellpes{
        text-overflow:ellipsis;
        overflow:hidden;
        display:-webkit-box;
        -webkit-line-clamp:5; /*这个数字是设置要显示省略号的行数*/
        -webkit-box-orient:vertical;
    }
</style>